import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaRadar=null;
export default {
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			chartData: {
				categories: ['记忆力', '注意力', '观察力', '思维力', '想象力'],
				series: [{
					name: '记忆力',
					// color: '#ffffff',
					data: [90, 110, 165, 195, 187]
				}, {
					name: '注意力',
					data: [190, 210, 105, 35, 27]
				}, {
					name: '观察力',
					data: [10, 20, 100, 35, 27]
				}, {
					name: '思维力',
					data: [101, 220, 140, 39, 50]
				}, {
					name: '想象力',
					data: [122, 320, 70, 300, 200]
				}]
			}
		}
	},
	onLoad() {
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData(){
			let _self = this
			let Radar={categories:[],series:[]};
			Radar.categories= _self.chartData.categories;
			Radar.series= _self.chartData.series;
			_self.showRadar("canvasRadar",Radar);
			// uni.request({
			// 	url: 'https://www.ucharts.cn/data.json',
			// 	data:{
			// 	},
			// 	success: function(res) {
			// 		console.log(res.data.data)
			// 		let Radar={categories:[],series:[]};
			// 		Radar.categories=res.data.data.Radar.categories;
			// 		Radar.series=res.data.data.Radar.series;
			// 		_self.showRadar("canvasRadar",Radar);
			// 	},
			// 	fail: () => {
			// 		_self.tips="网络错误，小程序端请检查合法域名";
			// 	},
			// });
		},
		showRadar(canvasId,chartData){
			canvaRadar=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'radar',
				fontSize:11,
				legend:{show:true},
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				animation: true,
				dataLabel: true,
				categories: chartData.categories,
				series: chartData.series,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					radar: {
						max: 200//雷达数值的最大值
					}
				}
			});
		}
	}
}
